export const onCatalogChanged = EventDefinition(
  'wix.restaurants.catalogs.v3.catalog_catalog_changed',
  true,
  (event: CatalogChangedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'data.catalog.draftPublishedDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<CatalogChangedEnvelope>();
export const onCatalogCreated = EventDefinition(
  'wix.restaurants.catalogs.v3.catalog_created',
  true,
  (event: CatalogCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.draftPublishedDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<CatalogCreatedEnvelope>();

